@charset "UTF-8";
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block; }

body {
  line-height: 1; }

ol,
ul {
  list-style: none; }

blockquote,
q {
  quotes: none; }

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

:root {
  --font-body: system-ui, -apple-system, BlinkMacSystemFont, “Segoe UI”, Roboto,
    Oxygen-Sans, Ubuntu, Cantarell, “Helvetica Neue”, Arial, sans-serif; }

a {
  color: #cf4647; }

body {
  background-color: #cf4647;
  color: #ffffff;
  font-size: 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  min-height: 100vh;
  font-family: var(--font-body);
  line-height: 1.4;
  text-align: center;
  padding: 2rem; }

header.main {
  display: flex;
  justify-content: center;
  align-items: center; }
  header.main .plus-sign {
    font-size: 2.5rem;
    display: inline-block;
    padding: 0.75em; }

img {
  max-width: 8rem; }

h1 {
  font-size: 2rem;
  font-weight: 200;
  margin-top: 2rem;
  margin-bottom: 1rem; }

p {
  line-height: 1.55; }

a {
  color: inherit;
  text-decoration: underline; }
  a:hover {
    text-decoration: none; }
